<template>
	<view>
		<!-- 地址 -->
		<navigator url="/pages/address/address?source=1" class="address-section">
			<view class="order-content">
				<text class="yticon icon-shouhuodizhi"></text>
				<view class="cen">
					<view class="top">
						<text class="name" v-if="! addressData.name">请添加地址</text>
						<text class="name">{{addressData.name}}</text>
						<text class="mobile">{{addressData.phoneNumber}}</text>
					</view>
					<text class="address">{{addressData.province}} {{addressData.city}} {{addressData.region}}
						{{addressData.detailAddress}}</text>
				</view>
				<text class="yticon icon-you"></text>
			</view>

			<image class="a-bg" src="data:image/png;base64,"></image>
		</navigator>

		<view class="goods-section">
			<view class="g-header b-b">
				<image class="logo" src="http://duoduo.qibukj.cn/./Upload/Images/20190321/201903211727515.png"></image>
				<text class="name">万诚小店</text>
			</view>
			<!-- 商品列表 -->
			<view class="g-item" v-for="(item, index) in orderInfo.list" :key="index">
				<image :src="encodeURI(item.productPic)"></image>
				<view class="right">
					<text class="title clamp">{{item.productName}}</text>
					<text class="spec">{{item.attr}}</text>
					<view class="price-box">
						<text class="price">￥{{item.price}}</text>
						<text class="number">x {{item.quantity}}</text>
					</view>
				</view>
			</view>
			<!-- <view class="g-item">
				<image src="https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1620020012,789258862&fm=26&gp=0.jpg"></image>
				<view class="right">
					<text class="title clamp">韩版于是洞洞拖鞋 夏季浴室防滑简约居家【新人专享，限选意见】</text>
					<text class="spec">春装款 L</text>
					<view class="price-box">
						<text class="price">￥17.8</text>
						<text class="number">x 1</text>
					</view>
				</view>
			</view> -->
		</view>

		<!-- 优惠明细 -->
		<view class="yt-list">
			<view class="yt-list-cell b-b" @click="toggleMask('show')">
				<view class="cell-icon">
					券
				</view>
				<text class="cell-tit clamp">优惠券</text>
				<text class="cell-tip active">
					{{activeMsg}}
				</text>
				<text class="cell-more wanjia wanjia-gengduo-d"></text>
			</view>
			<view class="yt-list-cell b-b">
				<view class="cell-icon hb">
					减
				</view>
				<text class="cell-tit clamp">商家促销</text>
				<text class="cell-tip disabled">暂无可用优惠</text>
			</view>
		</view>
		<!-- 金额明细 -->
		<view class="yt-list">
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">商品金额</text>
				<text class="cell-tip">￥{{orderInfo.totalAmount}}</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">优惠金额</text>
				<text class="cell-tip red">-￥{{discount}}</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">运费</text>
				<text class="cell-tip" v-if="orderInfo.freightAmount === 0">免运费</text>
				<text class="cell-tip" v-if="orderInfo.freightAmount > 0">{{orderInfo.freightAmount}}</text>
			</view>
			<view class="yt-list-cell desc-cell">
				<text class="cell-tit clamp">备注</text>
				<input class="desc" type="text" v-model="desc" placeholder="请填写备注信息" placeholder-class="placeholder" />
			</view>
		</view>

		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>实付款</text>
				<text class="price-tip">￥</text>
				<text class="price">{{orderInfo.freightAmount + orderInfo.totalAmount - discount}}</text>
			</view>
			<text class="submit" @click="submit">提交订单</text>
		</view>

		<!-- 优惠券面板 -->
		<view class="mask" :class="maskState===0 ? 'none' : maskState===1 ? 'show' : ''" @click="toggleMask">
			<view class="mask-content" @click.stop.prevent="stopPrevent">
				<!-- 优惠券页面，仿mt -->
				<view class="coupon-item" v-for="(item,index) in couponList" :key="index" @click="chooseCoupon(item)">
					<view class="con">
						<view class="left">
							<text class="title">{{item.name}}</text>
							<text class="time">有效期至{{item.endTime}}</text>
						</view>
						<view class="right">
							<text class="price">{{item.amount}}</text>
							<text>满{{item.minPoint}}可用</text>
						</view>

						<view class="circle l"></view>
						<view class="circle r"></view>
					</view>
					<text class="tips">{{item.note}}</text>
				</view>
				<view class="coupon-item" @click="chooseCoupon(0)">
					<view class="con">不使用优惠</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	import Api from 'common/js/model.js';
	export default {
		data() {
			return {
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				couponList: [],
				addressData: {},
				activeMsg:"选择优惠券",
				couponId:null,//当前选中的优惠券id
				discount:0,
				orderInfo: {
					freightAmount: 0,
					list: [],
					totalAmount: 0
				},
				confirmOrderParam: {

				}
			}
		},
		filters: {
			//2019-04-06 11:37
			time: function time(value) {
				var year = value.substr(0, 4)
				var month = value.substr(5, 2)
				var day = value.substr(8, 2)
				var hour = value.substr(11, 2)
				var min = value.substr(14, 2)
				var seconds = value.substr(17, 2)
				return year + "-" + month + "-" + day + " " + hour + ":" + min + ":" + seconds;
			},
			isEmpty: function isEmpty(value) {
				if (value) {
					return value;
				} else {
					return "";
				}
			}
		},
		onLoad(option) {
			var _self = this;
			//商品数据
			//let data = JSON.parse(option.data);
			if (option.addressId) {
				_self.getReceiptInfoById();
			} else {
				_self.getDefaultReceipt();
			}
			//确认订单信息
			var cartIds = option.cartIds;
			var cartIdArr;
			if (cartIds) {
				cartIdArr = cartIds.split(',');
			}
			var skuId = option.skuId;
			var num = option.num;
			_self.confirmOrderParam = {
				'cartIds': cartIdArr,
				'skuId': skuId,
				'num': num
			}
			_self.confirmOrder(_self.confirmOrderParam);
			_self.checkCouponsUsable();
		},
		methods: {
			//确认订单信息
			confirmOrder(param) {
				var _self = this;
				Api.methods.confirmOrder(param).then(function(res) {
					if (res.data.code === 200) {
						_self.orderInfo = res.data.data;
						_self.orderInfo.list.forEach(item =>{
							let attr = JSON.parse(item.productAttr)
							item.attr = ''
							for(var i =0;i<attr.length;i++){
								item.attr += attr[i].value+" "
							}
						})
					} else {
						_self.$api.msg('订单信息有误')
					}

				})
			},
			//通过id获取收货地址
			async getReceiptInfoById() {
				var _self = this
				Api.methods.getReceiptInfoById({
					id: option.addressId
				}).then(function(res) {
					_self.addressData = res.data.data
				})
			},
			//获取默认地址
			async getDefaultReceipt() {
				var _self = this
				Api.methods.getDefaultReceiptInfo().then(function(res) {
					var result = res.data
					if (result.code === 200) {
						_self.addressData = res.data.data
					} else {

					}
				})
			},
			//检查当前用户当前商品可用优惠券
			checkCouponsUsable(){
				var _self = this;
				Api.methods.checkCouponsUsable(_self.confirmOrderParam).then(res =>{
					if(res.data.code == 200){
						_self.couponList = res.data.data;
					}
				})
			},
			//显示优惠券面板
			toggleMask(type) {
				if(this.couponList.length<1){
					this.$api.msg('暂无可用优惠卷')
					return false; 
				}
				let timer = type === 'show' ? 10 : 300;
				let state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(() => {
					this.maskState = state;
				}, timer)
			},
			//选择优惠券
			chooseCoupon(coupon){
				var _self = this;
				_self.toggleMask(null);
				if(coupon == 0){
					_self.discount = 0;
					_self.activeMsg = "不使用优惠";
					_self.couponId = null;
					return;
				}
				_self.couponId = coupon.id;
				_self.discount = coupon.amount;
				_self.activeMsg = coupon.name+" ￥"+coupon.amount;
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type) {
				this.payType = type;
			},
			submit() {
				//第一 创建订单
				//第二 去付款
				var _self = this;
				if(JSON.stringify(_self.addressData) == "{}"){
					_self.$api.msg('请选择收货地址！');
					return;
				}
				var param = {
					addressId: _self.addressData.id,
					note: _self.desc,
					couponId: _self.couponId
				}
				if (_self.confirmOrderParam.cartIds) {
					param.cartIds = _self.confirmOrderParam.cartIds
					Api.methods.addCartOrder(param).then(function(res) {
						var result = res.data
						if (result.code === 200) {
							uni.redirectTo({
								url: '/pages/money/pay?orderSn=' + result.data.orderSn
							})
						} else {

						}
					})
				} else {
					param.num = _self.confirmOrderParam.num;
					param.skuId = _self.confirmOrderParam.skuId;
					Api.methods.createOrder(param).then(function(res) {
						var result = res.data
						if (result.code === 200) {
							uni.redirectTo({
								url: '/pages/money/pay?orderSn=' + result.data.orderSn
							})
						} else {

						}
					})
				}


			},
			stopPrevent() {}
		}
	}
</script>

<style lang="scss">
	page {
		background: $page-color-base;
		padding-bottom: 100upx;
	}

	.address-section {
		padding: 30upx 0;
		background: #fff;
		position: relative;

		.order-content {
			display: flex;
			align-items: center;
		}

		.icon-shouhuodizhi {
			flex-shrink: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 90upx;
			color: #888;
			font-size: 44upx;
		}

		.cen {
			display: flex;
			flex-direction: column;
			flex: 1;
			font-size: 28upx;
			color: $font-color-dark;
		}

		.name {
			font-size: 34upx;
			margin-right: 24upx;
		}

		.address {
			margin-top: 16upx;
			margin-right: 20upx;
			color: $font-color-light;
		}

		.icon-you {
			font-size: 32upx;
			color: $font-color-light;
			margin-right: 30upx;
		}

		.a-bg {
			position: absolute;
			left: 0;
			bottom: 0;
			display: block;
			width: 100%;
			height: 5upx;
		}
	}

	.goods-section {
		margin-top: 16upx;
		background: #fff;
		padding-bottom: 1px;

		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}

		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}

		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}

		.g-item {
			display: flex;
			margin: 20upx 30upx;

			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}

			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}

			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}

			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}

			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;

				.price {
					margin-bottom: 4upx;
				}

				.number {
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
			}

			.step-box {
				position: relative;
			}
		}
	}

	.yt-list {
		margin-top: 16upx;
		background: #fff;
	}

	.yt-list-cell {
		display: flex;
		align-items: center;
		padding: 10upx 30upx 10upx 40upx;
		line-height: 70upx;
		position: relative;

		&.cell-hover {
			background: #fafafa;
		}

		&.b-b:after {
			left: 30upx;
		}

		.cell-icon {
			height: 32upx;
			width: 32upx;
			font-size: 22upx;
			color: #fff;
			text-align: center;
			line-height: 32upx;
			background: #f85e52;
			border-radius: 4upx;
			margin-right: 12upx;

			&.hb {
				background: #ffaa0e;
			}

			&.lpk {
				background: #3ab54a;
			}

		}

		.cell-more {
			align-self: center;
			font-size: 24upx;
			color: $font-color-light;
			margin-left: 8upx;
			margin-right: -10upx;
		}

		.cell-tit {
			flex: 1;
			font-size: 26upx;
			color: $font-color-light;
			margin-right: 10upx;
		}

		.cell-tip {
			font-size: 26upx;
			color: $font-color-dark;

			&.disabled {
				color: $font-color-light;
			}

			&.active {
				color: $base-color;
			}

			&.red {
				color: $base-color;
			}
		}

		&.desc-cell {
			.cell-tit {
				max-width: 90upx;
			}
		}

		.desc {
			flex: 1;
			font-size: $font-base;
			color: $font-color-dark;
		}
	}

	/* 支付列表 */
	.pay-list {
		padding-left: 40upx;
		margin-top: 16upx;
		background: #fff;

		.pay-item {
			display: flex;
			align-items: center;
			padding-right: 20upx;
			line-height: 1;
			height: 110upx;
			position: relative;
		}

		.icon-weixinzhifu {
			width: 80upx;
			font-size: 40upx;
			color: #6BCC03;
		}

		.icon-alipay {
			width: 80upx;
			font-size: 40upx;
			color: #06B4FD;
		}

		.icon-xuanzhong2 {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 60upx;
			height: 60upx;
			font-size: 40upx;
			color: $base-color;
		}

		.tit {
			font-size: 32upx;
			color: $font-color-dark;
			flex: 1;
		}
	}

	.footer {
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0, 0, 0, .1);

		.price-content {
			padding-left: 30upx;
		}

		.price-tip {
			color: $base-color;
			margin-left: 8upx;
		}

		.price {
			font-size: 36upx;
			color: $base-color;
		}

		.submit {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
			background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
		}
	}

	/* 优惠券面板 */
	.mask {
		display: flex;
		align-items: flex-end;
		position: fixed;
		left: 0;
		top: var(--window-top);
		bottom: 0;
		width: 100%;
		background: rgba(0, 0, 0, 0);
		z-index: 9995;
		transition: .3s;

		.mask-content {
			width: 100%;
			min-height: 30vh;
			max-height: 70vh;
			background: #f3f3f3;
			transform: translateY(100%);
			transition: .3s;
			overflow-y: scroll;
		}

		&.none {
			display: none;
		}

		&.show {
			background: rgba(0, 0, 0, .4);

			.mask-content {
				transform: translateY(0);
			}
		}
	}

	/* 优惠券列表 */
	.coupon-item {
		display: flex;
		flex-direction: column;
		margin: 20upx 24upx;
		background: #fff;

		.con {
			display: flex;
			align-items: center;
			position: relative;
			height: 120upx;
			padding: 0 30upx;

			&:after {
				position: absolute;
				left: 0;
				bottom: 0;
				content: '';
				width: 100%;
				height: 0;
				border-bottom: 1px dashed #f3f3f3;
				transform: scaleY(50%);
			}
		}

		.left {
			display: flex;
			flex-direction: column;
			justify-content: center;
			flex: 1;
			overflow: hidden;
			height: 100upx;
		}

		.title {
			font-size: 32upx;
			color: $font-color-dark;
			margin-bottom: 10upx;
		}

		.time {
			font-size: 24upx;
			color: $font-color-light;
		}

		.right {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			font-size: 26upx;
			color: $font-color-base;
			height: 100upx;
		}

		.price {
			font-size: 44upx;
			color: $base-color;

			&:before {
				content: '￥';
				font-size: 34upx;
			}
		}

		.tips {
			font-size: 24upx;
			color: $font-color-light;
			line-height: 60upx;
			padding-left: 30upx;
		}

		.circle {
			position: absolute;
			left: -6upx;
			bottom: -10upx;
			z-index: 10;
			width: 20upx;
			height: 20upx;
			background: #f3f3f3;
			border-radius: 100px;

			&.r {
				left: auto;
				right: -6upx;
			}
		}
	}
</style>
